import React from "react";
import { BrowserRouter as Router, Route, Link } from "react-router-dom";
import './App.css';
import 'tachyons';
import logo from './logo.svg';

const CustomLinkExample = () => (
  <header className="App-header">
    <Router>
      <div>
        <div className="tc">
          <img src={logo} className="App-logo" alt="logo" />
          <h1 className="App-title">NanotechHub</h1>
          </div>
          <hr />
          <div className="tr">
            <OldSchoolMenuLink activeOnlyWhenExact={true} to="/" label="Home" />
            <OldSchoolMenuLink to="/about" label="About" />
            <OldSchoolMenuLink to="/resources" label="Resources" />
          </div>
          <Route exact path="/" component={Home} />
          <Route path="/about" component={About} />
          <Route path="/resources" component={Resources} />
        </div>  
      </Router>
    </header>  
);

const OldSchoolMenuLink = ({ label, to, activeOnlyWhenExact }) => (
  <Route
    path={to}
    exact={activeOnlyWhenExact}
    children={({ match }) => (
      <div className={match ? "active" : ""}>
        {match ? "> " : ""}
        <Link to={to}>{label}</Link>
      </div>
    )}
  />
);

const Home = () => (
  <div className="tc">
    <h4>“Right now, and for the first time ever, a passionate and committed individual has access to the technology, minds, and capital required to take on any challenge.”</h4>
    <h4>Dr. Peter H. Diamandis</h4>
  </div>
);

const Resources = () => (
  <div className="tc">
    <h2>Resources</h2>
    <h3><a href="https://www.youtube.com/watch?v=XZfUJuSmBAs">Biotechnology/Nanotechnology | Andrew Hessel | SingularityU Germany Summit 2017</a></h3>
  </div>
);

const About = () => (
  <div className="tc">
    <h2>About</h2>
    <h4>a project pursuing biotech and nanotech. </h4>
  <h4>For more projects! please visit my website: <a href='https://migueldeguzman.github.io'>https://migueldeguzman.github.io</a> </h4>
  </div>
);

export default CustomLinkExample;